import Vue from 'vue'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
import Axios from 'axios'
import qs from 'qs'
import envConfig from '@/http/env'
import router from '@/router'
// import iView from 'iview'

// API文档地址https://github.com/mzabriskie/axios
const Http = (Vue, options) => {
    let baseUrl = envConfig.ENV === 'production'?envConfig.prodBaseUrl : 'api'
    // let baseUrl = envConfig.prodBaseUrl
    Axios.defaults.timeout = 30000
    Axios.defaults.baseURL = baseUrl
    //Axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
    Axios.defaults.headers.post['Content-Type'] = 'application/json';
    Axios.defaults.headers.get['Content-Type'] = 'application/json';
    // 请求拦截器
    Axios.interceptors.request.use(config => {
        const token = sessionStorage.getItem('token');
        config.url=config.url+"&t="+token;

        console.log(config.url)

        // let USI = Vue.prototype.$Auth.getCookie('token');
        // if(!config.url.includes('ossUser/login')){
        //     if (USI) {
        //         //全局默认加入token和用户ID
        //        if(config.method === 'post') {
        //            config.data = {
        //                 ...config.data,
        //                 token: USI,
        //                 ossUserId: window.localStorage.ms_username,
        //            }
        //        }
        //     }
        // }


      return config
    }, err => Promise.reject(err))

    // 响应拦截器
    Axios.interceptors.response.use(config => {
         if (config.data.status==0) {
            // this.$Message.success("操作成功");
            // alert("sadsasa")
        }else if(config.data.status==200){
            router.push({path:"/login"});
            return;
        }else{
             iView.Message.error(config.data.msg);
             return;
        }
        // console.log(config.data)

        // if (!config.data.success ) {
        //     Vue.prototype.$notify({
        //         title: '警告',
        //         message: config.data.errorMsg,
        //         type: 'warning'
        //     });
        // }

        return {
            body: config.data,
            status: {
                code: config.status,
                message: config.statusText
            },
            headers(name) {
                return config.request.getResponseHeader(name)
            }
        }

    }, err => {

        iView.Message.error("网络异常，请稍后重试");
        // Vue.prototype.$notify.error({
        //     title: '错误',
        //     message: config.data.errorMsg
        //   });
    })

    return Axios
  }

  export default Http



